    // The following values of object below have to match those of enum HostFrameTrustState
// in % SRCROOT %\msoserviceplatform\src\Browser\App\Scripts\AFrameworkApplication.cs
/// <summary>
/// Different possible values for IsHostFrameTrusted
/// </summary>
    export enum HostFrameTrustState {
        /// <summary>
        /// Default, we haven't checked WOPI UI capablity yet, we are waiting to activate Add-Ins
        /// </summary>
        NotInitialized = 0,
        /// <summary>
        /// we are not activating Add-Ins
        /// </summary>
        NotImplemented = 1,
        /// <summary>
        /// Host indicated via WOPI UI capability it can handle the message, but no response yet. We are waiting to activate Add-ins.
        /// </summary>
        NotReady = 2,
        /// <summary>
        /// Temporary list of hosts that are exempted from sending Host_IsFrameTrusted, set if HostExemptedFromFrameTrustCheck is true
        /// </summary>
        Exempted = 3,
        /// <summary>
        /// Host has failed the trust check as it has been embedded without its consent
        /// </summary>
        NotTrusted = 4,
        /// <summary>
        /// Host confirmed via Host_IsFrameTursted post message that its embedded in its own domain
        /// </summary>
        Trusted = 5
    }
