    export class TitleDefinition implements ITitleDefinition {
        name!: string;
        iconSmall!: IconSmall;
        version!: string;
        elementDefinitions!: ElementDefinitions;
        ingestionSource!: string;
        scope!: string;
        developerName!: string;
        shortDescription!: string;
        /** A list of valid domains from which the tabs expect to load any content. Domain listings can include wildcards, for example `*.example.com`. If your tab configuration or content UI needs to navigate to any other domain besides the one use for tab configuration, that domain must be specified here. */
        validDomains!: string[];
        /** Specify your AAD App ID and Graph information to help users seamlessly sign into your AAD app. */
        webApplicationInfo!: WebApplicationInfo;
        /** Specify and consolidates authorization related information for the App. */
        authorization!: Authorization;

        constructor(data?: ITitleDefinition) {
            if (data) {
                for (var property in data) {
                    if (data.hasOwnProperty(property))
                        (<any>this)[property] = (<any>data)[property];
                }
            }
            if (!data) {
                this.iconSmall = new IconSmall();
                this.elementDefinitions = new ElementDefinitions();
            }
        }

        init(_data?: any) {
            if (_data) {
                this.name = _data["name"];
                this.iconSmall = _data["iconSmall"] ? IconSmall.fromJS(_data["iconSmall"]) : new IconSmall();
                this.version = _data["version"];
                this.elementDefinitions = _data["elementDefinitions"] ? ElementDefinitions.fromJS(_data["elementDefinitions"]) : new ElementDefinitions();
                this.ingestionSource = _data["ingestionSource"];
                this.scope = _data["scope"];
                this.developerName = _data["developerName"];
                this.shortDescription = _data["shortDescription"];
                if (Array.isArray(_data["validDomains"])) {
                    this.validDomains = [] as any;
                    for (let item of _data["validDomains"])
                        this.validDomains!.push(item);
                }
                this.webApplicationInfo = _data["webApplicationInfo"] ? WebApplicationInfo.fromJS(_data["webApplicationInfo"]) : <any>undefined;
                this.authorization = _data["authorization"] ? Authorization.fromJS(_data["authorization"]) : <any>undefined;
            }
        }

        static fromJS(data: any): TitleDefinition {
            data = typeof data === 'object' ? data : {};
            let result = new TitleDefinition();
            result.init(data);
            return result;
        }

        toJSON(data?: any) {
            data = typeof data === 'object' ? data : {};
            data["name"] = this.name;
            data["iconSmall"] = this.iconSmall ? this.iconSmall.toJSON() : <any>undefined;
            data["version"] = this.version;
            data["elementDefinitions"] = this.elementDefinitions ? this.elementDefinitions.toJSON() : <any>undefined;
            data["ingestionSource"] = this.ingestionSource;
            data["scope"] = this.scope;
            data["developerName"] = this.developerName;
            data["shortDescription"] = this.shortDescription;
            if (Array.isArray(this.validDomains)) {
                data["validDomains"] = [];
                for (let item of this.validDomains)
                    data["validDomains"].push(item);
            }
            data["webApplicationInfo"] = this.webApplicationInfo ? this.webApplicationInfo.toJSON() : <any>undefined;
            data["authorization"] = this.authorization ? this.authorization.toJSON() : <any>undefined;
            return data;
        }
    }

    export interface ITitleDefinition {
        name: string;
        iconSmall: IconSmall;
        version: string;
        elementDefinitions: ElementDefinitions;
        ingestionSource: string;
        scope: string;
        developerName: string;
        shortDescription: string;
        /** A list of valid domains from which the tabs expect to load any content. Domain listings can include wildcards, for example `*.example.com`. If your tab configuration or content UI needs to navigate to any other domain besides the one use for tab configuration, that domain must be specified here. */
        validDomains: string[];
        /** Specify your AAD App ID and Graph information to help users seamlessly sign into your AAD app. */
        webApplicationInfo: WebApplicationInfo;
        /** Specify and consolidates authorization related information for the App. */
        authorization: Authorization;
    }

