    /// <param name="requirementMatrix">JSON serialized string containing supported API. (Type: String)</param>
    /// <param name="hostCustomMessage">Allows the host to pass a custom message to office.js telemetry. (Type: String)</param>
    /// <param name="hostFullVersion">The major/minor/revision/sequence version of the host. (Type: String)</param>
    /// <param name="clientWindowHeight">The height of the host window. (Type: Integer)</param>
    /// <param name="clientWindowWidth">The width of the host window. (Type: Integer)</param>
    /// <param name="addinName">The name of current addin (Type: String)</param>
    /// <param name="appDomains">The app domain list of the agave (Type: Array)</param>
    /// <param name="dialogRequirementMatrix">JSON serialized string containing dialog requirements. (Type: String)</param>
